<template>
  <div id="githubproject">
    <b-card
      :title="project.name"
      :img-src="project.image"
      class="shadow-sm h-100"
      bg-variant="dark"
      border-variant="dark"
      text-variant="white"
      overlay
      v-on:click="handleProject(project.name)"
    >
    </b-card>
  </div>
</template>

<script>
export default {
  name: "GithubProject",
  props: {
    project: {
      type: Object
    }
  },
  methods: {
    handleProject(name) {
      this.$router.push({ name: "project", params: { name: name } });
    }
  }
};
</script>

